body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.image {
  width: 100%;
  height: 300px;
  display: flex;
  align-items: center;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

button.a {
  padding: 0;
  margin: 0;
}

.plus,
.minus {
  box-shadow: 0 0 10px #888;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
}

.plus:before,
.plus:after,
.minus:before {
  content: "";
  display: block;
  background: #4f6f94;
  position: relative;
  height: 20px;
  width: 5px;
  border-radius: 2.5px;
}

.plus:before,
.minus:before {
  top: 10px;
  left: 17.5px;
  transform: rotate(-90deg);
}

.plus:after {
  top: -10px;
  left: 17.5px;
}
